
body {
		padding-top: 50px;
}
.slide-enter-active {
    -webkit-animation: slide-in ease-out 200ms forwards;
            animation: slide-in ease-out 200ms forwards;
}
.slide-leave-active {
    -webkit-animation: slide-out ease-out 200ms forwards;
            animation: slide-out ease-out 200ms forwards;
}
@-webkit-keyframes slide-in {
from {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
      opacity: 0;
}
to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
}
}
@keyframes slide-in {
from {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
      opacity: 0;
}
to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
}
}
@-webkit-keyframes slide-out {
from {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
}
to {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
      opacity: 0;
}
}
@keyframes slide-out {
from {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
}
to {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
      opacity: 0;
}
}	

